.calculation{      
    padding: 98px 0px 113px;    
    .container{
        position: relative;
        margin: 0 auto;
        max-width: 1182px;
        width: 100%;
        padding: 0 22px;
        .template-title {
            padding-bottom: 52px;
        }
        .calculation-row{
            display: flex;         
            .calculation-form {
                max-width: 626px;
                width: 100%;            
                border-radius: 20px;
                background-color: #363A4D;
                padding: 50px 50px 55px;
                box-shadow: 5px 15px 40px 0 rgba(11, 24, 97, 0.32);
                .form-row{
                    display: flex;
                    gap: 30px;
                    .form-column{
                        width: 100%;
                        .form-input{
                            display: flex;
                            flex-direction: column;
                            margin-bottom: 20px;
                            position: relative;
                            .file-name{
                                position: absolute;
                                left: 0;
                                top: 12px;
                                font-weight: 400;
                                font-size: 15px;
                                line-height: 126%;
                            }
                            label{
                                margin-bottom: 6px;        
                                font-weight: 600;
                                font-size: 15px;
                                line-height: 126%;
                            }
                            input{
                                border-radius: 10px;       
                                padding: 17px 18px;
                                font-weight: 600;
                                font-size: 15px;
                                line-height: 126%;
                                color:$color;
                                    &::placeholder{            
                                        font-weight: 400;
                                        font-size: 15px;
                                        line-height: 126%;
                                        color: #cbcbcb;
                                    }
                                    &:focus-visible{
                                        outline: none; 
                                    }
                                    &:hover{
                                        box-shadow: 0px 0px 1px 1px $primary-color;
                                    }
                                    &:focus{
                                        box-shadow: 0px 0px 1px 1px $secondary-color;
                                    }
                                    &:active{
                                        box-shadow: 0px 0px 1px 2px $secondary-color;
                                    }         
                                }    
                        }
                        .control-box{
                            position: relative;
                            margin-bottom: 14px;
                            &:first-child{
                                margin-top: 19px;
                            }
                        }  
                    }     
                }
                     
            } 
            .calculation-img{
                position: absolute;           
                left: 618px;
                bottom: -112px;           
                max-width: 560px;           
                width: 29.167vw;  
                min-width: 400px;             
            }     
        }
    }
}
  